













import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class Todo extends Vue {
  @Prop() private msg!: string;
  newTodo='';
  numberOfTask = 3;
  todos=[
      {
      id:1,
      task:'Creating todo app with vue.js',
      finished:true,  
      edit:false,
    },
    {
      id:2,
      task:'Earning $1 million.',
      finished:false,  
      edit:false,
    },
  ];


  addTodo() {
    if(this.newTodo.trim() ==''){
      return;
    }
      this.todos.push({
          id:this.numberOfTask,
          task:this.newTodo,
          finished:false,
          edit:false,

      });
    this.numberOfTask++;
    this.newTodo = '';
    }

    removeTodo(index) {
      this.todos.splice(index, 1);
    }

    edit(todo){
      todo.edit =true

    }

    editDone(todo) {
      todo.edit = false

    }

}
